import {Component, OnInit} from '@angular/core';
import {AuthService, CoreService} from '@core/services';
import {BsModalRef} from 'ngx-bootstrap';
import {Router} from '@angular/router';

@Component({
  selector: 'app-notify-invoice-expired',
  templateUrl: './notify-invoice-expired.component.html',
  styleUrls: ['./notify-invoice-expired.component.scss']
})
export class NotifyInvoiceExpiredComponent implements OnInit {
  _translate: any;
  dataAmount: any;
  isCheck = false;
  tax_code: number;
  arrCookie = [];
  valueCokie: number;

  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    public coreService: CoreService,
    public router: Router,
  ) {
    this._translate = this.authService.getTranslate();
  }

  ngOnInit() {
  }

  close() {
    document.cookie = 'isNotifyUser=' + JSON.stringify(this.checkCookie());
    this.bsModalRef.hide();
  }

  dongy() {
    document.cookie = 'isNotifyUser=' + JSON.stringify(this.checkCookie());
    this.bsModalRef.content.successSaveItem = 'SuccessHomePage';
    this.bsModalRef.hide();
  }

  checkCookie() {
    if (this.isCheck === true) {
      this.valueCokie = 1;
    } else {
      this.valueCokie = 0;
    }
    this.arrCookie = [];
    const ca = document.cookie.split(';');
    const name = 'isNotifyUser';
    for (let i = 0; i < ca.length; i++) {
      const c = ca[i].split('=');
      if (name === c[0].trim()) {
        this.arrCookie = JSON.parse(c[1]);
      }
    }
    if (this.arrCookie.length > 0) {
      const value = this.arrCookie.some(element => element.tax_code === this.tax_code);
      if (value) {
        for (let i = 0; i < this.arrCookie.length; i++) {
          if (this.tax_code === this.arrCookie[i].tax_code) {
            this.arrCookie[i].isCheck = this.valueCokie;
          }
        }
      } else {
        this.arrCookie.push({isCheck: this.valueCokie, tax_code: this.tax_code});
      }
    } else {
      this.arrCookie.push({isCheck: this.valueCokie, tax_code: this.tax_code});
    }
    return this.arrCookie;
  }

  onChangeCheck() {
    this.isCheck = !this.isCheck;
  }
}
