import {Component, OnInit, OnDestroy} from '@angular/core';
import {Router} from '@angular/router';

import {BsModalRef} from 'ngx-bootstrap/modal/bs-modal-ref.service';

import {CoreService, AuthService} from '@core/index';
import {TranslateService} from '@ngx-translate/core';

@Component({
  selector: 'app-nhac-viec',
  templateUrl: './nhac-viec.component.html',
  styleUrls: ['./nhac-viec.component.scss']
})
export class NhacViecComponent implements OnInit, OnDestroy {

  data;
  sub;
  conut1;
  conut2;
  conut3;
  conut4;
  conut5;
  conut6;
  _translate: any;

  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    public coreService: CoreService,
    public router: Router,
    public translate: TranslateService,
  ) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    this.data.forEach(item => {
      if (item.name == 'lxhd1') {
        this.conut1 = item;
      }
      if (item.name == 'lxhd2') {
        this.conut2 = item;
      }
      if (item.name == 'lxhd3') {
        this.conut3 = item;
      }
      if (item.name == 'baocao') {
        this.conut4 = item;
      }
      if (item.name == 'lxhd4') {
        this.conut5 = item;
      }
      if (item.name == 'lxhd5') {
        this.conut6 = item;
      }
    });
  }

  dongy() {
    this.bsModalRef.hide();
    this.coreService.showNhacViec = false;
  }

  goTo(data) {
    let param = data.param;
    param.send = param.send || null;
    param.status = param.status || null;
    param.errorSign = param.errorSign || null;
    param.is_advanced = true;
    param.approve_status = param.approve_status || null;
    this.router.navigate([data.link], {
      queryParams: param,
      queryParamsHandling: 'merge'
    });
    this.dongy();
  }

  daBaoCao() {
    this.coreService.daBaoCao();
    this.conut4 = null;
  }

  ngOnDestroy() {
    // this.sub.unsubscribe();
  }

}
