import { AfterViewInit, Component, EventEmitter, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { AuthService } from '@core/services/auth.service';
import { BsModalRef, ModalOptions } from 'ngx-bootstrap';
import { CoreService, Library } from '@core/index';
import { HomeService } from '@modules/home/shared';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { rtlMode } from '@syncfusion/ej2-angular-richtexteditor';

@Component({
  selector: 'app-survey-sheet',
  templateUrl: './survey-sheet.component.html',
  styleUrls: ['./survey-sheet.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class SurveySheetComponent implements OnInit, AfterViewInit {
  @Output() closeNotif = new EventEmitter();
  _translate: any;
  colorTitle: any;
  imgContent: any;
  dataSource: any;
  currentUser: any;
  unit_name: any;
  title = "Thông báo"
  auto_notify_cks_expire: any;
  auto_notify_error_mail: any;
  dataCSK: any = {};
  dataEmail: any;
  dataLicense: any;
  mail_license_error: any;
  arrId = [];
  province_name: any;
  list = [
    { id: 1, name: "Phần mềm hợp đồng điện tử", value: false },
    { id: 2, name: "Phần mềm bảo hiểm xã hội điện tử", value: false },
    { id: 3, name: "Chữ ký số", value: false },
    { id: 4, name: "Phần mềm hóa đơn điện tử", value: false },
    { id: 5, name: "Chứng từ khấu trừ thuế TNCN điện tử", value: false },
    { id: 6, name: "Phần mềm lưu trữ hóa đơn đầu vào", value: false },
    { id: 7, name: "Phiếu xuất kho kiêm vận chuyển nội bộ", value: false },
    { id: 8, name: "Phiếu thu", value: false }

  ];
  cities: any;
  formData: FormGroup;
  constructor(private authService: AuthService,
    public coreService: CoreService,
    public bsModalRef: BsModalRef,
    public homeService: HomeService,
    private fb: FormBuilder
  ) {
    this._translate = this.authService.getTranslate();
    this.homeService.getDataSelect().subscribe(res => {
      this.cities = res.data.DM_TINH_THANH
    });
    this.formData = this.fb.group({
      phone_number: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^(\(?\+?[0-9]*\)?)?[0-9_\- \(\)]*$/),
      ]),],
      email: ['', Validators.compose([
        Validators.required,
        Validators.pattern(/^(([a-zA-Z0-9_\-\.&]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([a-zA-Z0-9\-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)(\s*;\s*|\s*$))*$/),
      ]),],
      province: ['', Validators.compose([
        Validators.required])]
    });
  }
  ngOnInit() {
    this.homeService.getInforDeclare().subscribe(res => {
      this.province_name = res.data.province_name;
      this.formData.get('province').setValue(res.data.province_name);
    })
  }
  ngAfterViewInit() {

  }
  dong() {
    this.bsModalRef.hide();
    this.homeService.closeSurveySheet().subscribe(res => {
      if (res.status == 'success') {
        let currentUser = this.authService.getCurrentUser();
        currentUser.display_popup_demand_survey = false;
        this.authService.setCurentAuth(currentUser);
      }
    });
  }
  validForm() {
    let valid_phone = this.formData.get("phone_number").errors;
    let valid_mail = this.formData.get("email").errors;
    let valid_city = this.formData.get("province").errors;
    let list_service = this.list.filter(item => item.value);
    if (this.formData.status == "INVALID" || list_service.length === 0) {
      if (valid_phone) {
        if (valid_phone.pattern) {
          Library.notify('Vui lòng nhập số điện thoại đúng định dạng', 'error');
          return false
        }
        if (valid_phone.required) {
          Library.notify('Vui lòng nhập số điện thoại', 'error');
          return false
        }
      }
      if (valid_mail) {
        if (valid_mail.pattern) {
          Library.notify('Vui lòng nhập địa chỉ email đúng định dạng', 'error');
          return false
        }
        if (valid_mail.required) {
          Library.notify('Vui lòng nhập địa chỉ email', 'error');
          return false
        }
      }
      if (valid_city) {
        Library.notify('Vui lòng chọn Tỉnh/ Thành Phố', 'error');
        return false
      }
      if (list_service.length === 0) {
        Library.notify('Vui lòng chọn ít nhất một dịch vụ', 'error');
        return false
      }
    } else {
      return true;
    }
  }
  onSubmit() {
    if (!this.validForm()) {
      return;
    }
    let services = ''
    for (let i of this.list) {
      if (i.value) {
        services += i.name + ';';
      }
    }
    const data = this.formData.value
    data['services'] = services.slice(0, -1);
    this.homeService.sendSurveySheet(data).subscribe(res => {
      if (res.status == 'success') {
        Library.notify("Cảm ơn bạn đã thực hiện khảo sát", "success")
        this.dong();
      } else {
        Library.notify("thất bại", "error");
        this.bsModalRef.hide();
      }
    })
  }
}
