import { Component, EventEmitter, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { AuthService } from '@core/services/auth.service';
import { BsModalRef, ModalOptions } from 'ngx-bootstrap';
import { CoreService } from '@core/index';
import { Base64 } from 'js-base64';

@Component({
  selector: 'app-notification',
  templateUrl: './invoice-package.component.html',
  styleUrls: ['./invoice-package.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class InvoicePackageComponent implements OnInit {
  @Output() closeNotif = new EventEmitter();
  _translate: any;
  colorTitle: any;
  imgContent: any;
  dataSource: any;
  iframe: any;
  htmlString: any;
  title: any;
  isWarning: any;
  remaining_amount: number;
  arrData = [];
  currentUser: any;
  unit_name: any;
  constructor(private authService: AuthService,
    public coreService: CoreService,
    public bsModalRef: BsModalRef,
    public options: ModalOptions
  ) {
    this._translate = this.authService.getTranslate();

  }
  ngOnInit() {
    
  }
  dong() {
    this.bsModalRef.hide();
  }
}
