import { Component, EventEmitter, OnInit, Output, Pipe, PipeTransform, ViewEncapsulation } from '@angular/core';
import { AuthService } from '@core/services/auth.service';
import { BsModalRef, ModalOptions } from 'ngx-bootstrap';
import { CoreService } from '@core/index';
import { Base64 } from 'js-base64';
import { DomSanitizer } from '@angular/platform-browser';
import { HomeService } from '@modules/home/shared';

@Component({
  selector: 'app-notification',
  templateUrl: './notification.component.html',
  styleUrls: ['./notification.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class NotificationComponent implements OnInit {
  @Output() closeNotif = new EventEmitter();
  _translate: any;
  colorTitle: any;
  imgContent: any;
  dataSource: any;
  iframe: any;
  htmlString: any;
  title: any;
  arrData = [];
  currentUser: any;
  unit_name: any;
  constructor(private authService: AuthService,
    public coreService: CoreService,
    public bsModalRef: BsModalRef,
    public options: ModalOptions,
    private homeService: HomeService
  ) {
    this._translate = this.authService.getTranslate();

  }
  ngOnInit() {
    if (this.currentUser && this.currentUser.account)
      this.unit_name = this.currentUser.account.name;
    this.dataSource = this.options.initialState;
    let firstNotify: any;
    let o_tct = this.dataSource.data.filter(p => p.token == "TCTPOB")[0];
    if (o_tct)
      firstNotify = o_tct;
    else
      firstNotify = this.dataSource.data[this.dataSource.data.length - 1];
    this.htmlString = Base64.decode(firstNotify.content);
    this.htmlString = this.htmlString.replace("_object_name_", this.unit_name);
    this.title = firstNotify.title;
    this.colorTitle = { color: '#C70000' };
    this.imgContent = "<img src=\"assets/img/icons/thong-bao-tap-huan.png\" width=\"735px\" height=\"auto\" >";
    // this.iframe = document.getElementById('preview-iframe');
    // this.iframe.contentDocument.open("text/html", "replace");
    // this.iframe.contentDocument.write(this.htmlString);
    // this.iframe.contentDocument.close();
    this.arrData.push(firstNotify.id);
  }
  dong() {
    let isConThongBao = false;
    for (let i = this.dataSource.data.length - 1; 0 <= i; i--) {
      if (this.arrData.indexOf(this.dataSource.data[i].id) < 0) {
        isConThongBao = true;
        this.htmlString = Base64.decode(this.dataSource.data[i].content);
        this.htmlString = this.htmlString.replace("_object_name_", this.unit_name);
        this.title = this.dataSource.data[i].title;
        // this.iframe.contentDocument.write(this.htmlString);
        // this.iframe.contentDocument.close();
        this.arrData.push(this.dataSource.data[i].id);
        break;
      }
    }
    if (!isConThongBao) {
      let idPopupArray = [];
      this.dataSource.data.forEach(item => {
        if (item.view_type == 'POPUP') {
          idPopupArray.push(item.id);
        }
      });
      if (idPopupArray.length > 0) {
        let sub = this.homeService.notifyClose(idPopupArray).subscribe(
          rs => { },
          err => { },
          () => {
            sub.unsubscribe();
          }
        )
      }
      this.coreService.showThongBaoNangCap = false;
      this.bsModalRef.hide();
      this.bsModalRef.content.successSaveItem = 'QUAY_LAI';
    }
  }
}
@Pipe({ name: "safeHtml" })
export class SafeHtmlPipe implements PipeTransform {
  constructor(private sanitizer: DomSanitizer) { }

  transform(value) {
    return this.sanitizer.bypassSecurityTrustHtml(value);
  }
}
