      let line2 = "<p><span>" + "Bạn vẫn còn hóa đơn chờ ký tại phần mềm iHoadon trên máy tính. Vui lòng kiểm tra và xử lý hết hóa đơn trước khi chuyển đổi sang sử dụng HĐĐT theo TT78" + "</span></p>";
