  dong() {
    this.bsModalRef.hide();
    if (this.typeNotify == sEnum.notify.HoaDonChoKy)
      this.coreService.showWarningInvoiceWait = false;
    else if (this.typeNotify == sEnum.notify.KiemTraThongTin)
      this.bsModalRef.content.successSaveItem = 'TU_CHOI';
    else if (this.typeNotify == sEnum.notify.NgungHoatDong)
      this.bsModalRef.content.successSaveItem = 'QUAY_LAI';
    else if (this.typeNotify == sEnum.notify.TaiKhoanTichHop)
      this.bsModalRef.content.successSaveItem = 'QUAY_LAI';
    else if (this.typeNotify == sEnum.notify.TonTaiHoaDonChoKy)
      this.bsModalRef.content.successSaveItem = 'QUAY_LAI';
    else if (this.typeNotify == sEnum.notify.ChuaApDungTT78)
      this.bsModalRef.content.successSaveItem = 'QUAY_LAI';
    else if (this.typeNotify == sEnum.notify.DaCoTaiKhoanV3 || this.typeNotify == sEnum.notify.NopToKhaiThanhCong || this.typeNotify == sEnum.notify.DangKyThanhCong) {
      this.bsModalRef.hide();
      this.router.navigate(['login']);
    }
  }
  DongY() {
    if (this.typeNotify == sEnum.notify.ChuyenDoiSDHDTT78) {
      if (this.displayStep1) {
        //Gọi api kiểm tra Còn hóa đơn chờ ký hay k
        Library.showloading();
        let subModal = this.infoService.getStatusInvoiceWait().subscribe((res) => {
          if (res.status == "success") {
            if (res.data.is_exist)
              Library.notify("Vui lòng xuất các hóa đơn chờ ký để chuyển đổi áp dụng HĐĐT theo TT78.", 'error');
            else {
              this.bsModalRef.hide();
              this.router.navigate(['register-global']);
            }
          }
          else {
            Library.notify(res.message, 'error');
          }
          subModal.unsubscribe();
        }, err => {
        }, () => {
          Library.hideloading();
        });
      }
      else {
        if (this.displayStep2) {
          this.bsModalRef.hide();
          this.router.navigate(['register-template']);
        }
        else if (this.displayStep3) {
          if (this.status_register == "CHAP_NHAN")//test if(this.status_register == "CHAP_NHAN")
          {
            this.bsModalRef.hide();
            this.router.navigate(['cancel-release-notify']);
          }
          else {
            Library.notify("Vui lòng chờ CQT xử lý tờ khai đăng ký trước khi thực hiện hủy hóa đơn theo quy định cũ", 'error', 5000);
          }
        }
      }
    }
    else if (this.typeNotify == sEnum.notify.KiemTraThongTin) {
      this.bsModalRef.content.successSaveItem = 'DONG_Y';
      this.bsModalRef.hide();
    }
    else if (this.typeNotify == sEnum.notify.DaCoTaiKhoanV3) {
      this.bsModalRef.hide();
      this.router.navigate(['login']);
    }
    else if (this.typeNotify == sEnum.notify.ChuaApDungTT78) {
      this.bsModalRef.content.successSaveItem = 'QUAY_LAI';
      this.bsModalRef.hide();
      this.router.navigate([]).then(result => { window.open('https://ihoadon.vn/register/', '_blank'); });
    }
  }
