import { Component, OnInit, OnDestroy, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { DxFormComponent } from 'devextreme-angular';
import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { UsersService } from '@modules/users/shared';
import { CoreService, AuthService, Library } from '@core/index';
import { TranslateService } from '@ngx-translate/core';
import { ConfigsService } from '@modules/configs/shared';
@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.scss']
})
export class ChangePasswordComponent implements OnInit {
  @ViewChild(DxFormComponent) form: DxFormComponent;
  _translate: any;
  passwordPattern: any = /^.{6,}$/;
  password = '';
  passwordOptions: any = {
    mode: 'password',
    value: this.password
  };
  user: any;
  showTextValidPass: any;
  configs: any;
  passwordComparison = () => {
    return this.form.instance.option('formData').new_password;
  };
  constructor(
    private authService: AuthService,
    public bsModalRef: BsModalRef,
    public coreService: CoreService,
    public router: Router,
    private usersService: UsersService,
    public configsService: ConfigsService,
    public translate: TranslateService,) {
    this._translate = this.authService.getTranslate();
    translate.use(this.authService.getLanguage());
  }

  ngOnInit() {
    this.user = {
      old_password: '',
      new_password: '',
    };
    this.configs = this.authService.getConfigs();
    this.showTextValidPass = this._translate.VALIDATION.than6;
    if (this.configs) {
      if (this.configs.system_parameter.is_regex_pass) {
        this.passwordPattern = /^(?=.*[!@#$%^&*-])(?=(.*[0-9]){2,})(?=.*[A-Z])(?=.*[a-z]).{8,20}$/;
        this.showTextValidPass = this._translate.CONTROLL.LABEL.regex_password;
      }
    }
    else {
      this.configsService.getAll().subscribe((res) => {
        if (res.data && res.data.configs) {
          this.authService.setConfigs(res.data.configs);
          this.configs = this.authService.getConfigs();
          if (this.configs.system_parameter.is_regex_pass) {
            this.passwordPattern = /^(?=.*[!@#$%^&*-])(?=(.*[0-9]){2,})(?=.*[A-Z])(?=.*[a-z]).{8,20}$/;
            this.showTextValidPass = this._translate.CONTROLL.LABEL.regex_password;
          }
        }
      });
    }
  }
  onSubmit(e) {
    this.coreService.showChangePassword = false;
    const data = {
      user: {
        old_password: this.user.old_password,
        new_password: this.user.new_password,
      }
    };
    Library.showloading();
    this.usersService.changePassword(data).subscribe(response => {
      if (response.status === 'success') {
        Library.notify(this._translate.NOTIFY.update_success, 'success');
        this.bsModalRef.hide();
      } else {
        Library.notify(response.message, 'error');
      }
    }, err => { Library.notify(err, 'error'); }, () => {
      Library.hideloading();
    });
  }
}
