import { Component, OnInit } from '@angular/core';

import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
import { Subscription } from 'rxjs/Subscription';

import { AuthService, Library, SocketService, Helper, CoreService } from '@core/index';
import { HomeService } from '@modules/home/shared';

@Component({
  selector: 'app-sign-contract',
  templateUrl: './sign-contract.component.html',
  styleUrls: ['./sign-contract.component.scss']
})
export class SignContractComponent implements OnInit {

  currentUser: any;
  packages: any;
  ioSignature: Subscription;
  packagesCanKy: Array<any> = [];

  constructor(
    public bsModalRef: BsModalRef,
    private authService: AuthService,
    private homeService: HomeService,
    private socketService: SocketService,
    private coreService: CoreService,
  ) { }

  ngOnInit() {
    this.currentUser = this.authService.getCurrentUser();

    this.connectWebsocket();

    this.homeService.getPackageContract().subscribe(rs => {
      if (rs.status == "success") {
        this.packages = rs.data.packages;
        this.packagesCanKy = this.packages.filter(item => item.unsigned == 1);
      } else {
        Library.notify(rs.message, 'error');
        this.bsModalRef.hide();
      }
    });
  }

  connectWebsocket() {
    this.ioSignature = this.socketService.lapHoaDon().subscribe(message => {
      let data: any = message;
      if (data.SvcName == "efy_Sign") {
        // Helper.dowloadFile('a.pdf', 'pdf', data.Data.Content);

        let f_data = {
          "customer": {
            "id": this.currentUser.account.van_account_id
          },
          "service": {
            "file_content": data.Data.Content 
          }
        }
        Library.showloading();
        this.homeService.kyHopDong(f_data).subscribe((res: any) => {
          if(res.status == "success"){
            Library.notify("Ký hợp đồng thành công", "success");
            let currentUser = this.authService.getCurrentUser();
            currentUser.unsigned = 0;
            localStorage.setItem('currentUser', JSON.stringify(currentUser));
            this.bsModalRef.hide();
          }else{
            Library.notify(res.message, "error");
          }
          Library.hideloading();
        });
        
      }
    });
  }

  viewContract(){
    if(this.packagesCanKy[0]){
      let win = window.open("hello","", "width=100%, height=auto");
      let pdf_file = this.packagesCanKy[0].content_contract;
      win.document.write(`<iframe width="100%" height="97%" src="data:application/pdf;base64,${pdf_file}"></iframe>`)
      win.resizeTo(screen.availWidth, screen.availHeight);
      win.focus();
    }
  }

  // khoiTao(formatter) {

  //   this.packages[0].payment_method_names.some(item => {
  //     if (item.is_default) {
  //       this.packages[0].payment_method_name = item.code;
  //     }
  //   });

  //   this.packages[0] = [];

  //   this.packages[0].month_regulation = this.currentUser.month_regulation;
  //   this.packages[0].package_code = this.currentUser.package_code;
  //   this.packages[0].amount = this.currentUser.amount;
  //   this.packages[0].registration_date = this.currentUser.registration_date;

  //   //set value default
  //   if (this.packages[0].month_regulation.length) {
  //     this.coreService.boquaKy = true;
  //   }
  // }

  onKy = (e) => {
    if (this.packagesCanKy.length) {
      this.socketService.kiFile(() => this.connectWebsocket(), this.packagesCanKy[0].content_contract, 'bien_ban.pdf', 'pdf',true, null, 'ALIGN_LEFT', 'PAGE_LAST');
    }
  }

  boquaky() {
    this.bsModalRef.hide();
    this.coreService.boquaKy = true;
  }

}
