import { AfterViewInit, Component, EventEmitter, OnInit, Output, ViewEncapsulation } from '@angular/core';
import { AuthService } from '@core/services/auth.service';
import { BsModalRef, ModalOptions } from 'ngx-bootstrap';
import { CoreService, Library } from '@core/index';
import { HomeService } from '@modules/home/shared';

@Component({
  selector: 'app-notification',
  templateUrl: './notify-mail-csk.component.html',
  styleUrls: ['./notify-mail-csk.component.scss'],
  encapsulation: ViewEncapsulation.None,
})
export class NotifyMailCskComponent implements OnInit, AfterViewInit {
  @Output() closeNotif = new EventEmitter();
  _translate: any;
  colorTitle: any;
  imgContent: any;
  dataSource: any;
  currentUser: any;
  unit_name: any;
  title = "Thông báo"
  auto_notify_cks_expire: any;
  auto_notify_error_mail: any;
  dataCSK: any = {};
  dataEmail: any;
  dataLicense: any;
  mail_license_error: any;
  arrId = [];
  constructor(private authService: AuthService,
    public coreService: CoreService,
    public bsModalRef: BsModalRef,
    public homeService: HomeService
  ) {
    this._translate = this.authService.getTranslate();
  }
  ngOnInit() {

  }
  delete(data) {
    let error_mails = {
      error_mails: [data.id]
    }
    const result = Library.confirm(this._translate.CONFIRM.delete, this._translate.NOTIFY.notify);
    result.then((dialogResult) => {
      if (dialogResult) {
        Library.showloading();
        this.homeService.deleteMailError(error_mails).subscribe(rs => {
          if (rs.status === 'success') {
            this.homeService.getListEMailError().subscribe(res => {
              this.dataEmail = res.data.mail_error_notifications.list;
              this.dataLicense = res.data.mail_license_error_notifications.list;
              if (this.dataEmail && this.dataEmail.length > 0) {
                this.auto_notify_error_mail = true;
              } else {
                this.auto_notify_error_mail = false;
              }
              if (this.dataLicense && this.dataLicense.length > 0) {
                this.mail_license_error = true;
              } else {
                this.mail_license_error = false;
              }
              if (this.dataEmail.length == 0 && this.dataLicense.length == 0) {
                this.bsModalRef.hide();
              }
              Library.notify(this._translate.NOTIFY.delete_success, 'success');
            })
          } else {
            Library.notify(rs.message, 'error');
          }
        }, err => {
        }, () => {
          Library.hideloading();
        });
      }
    });
  }
  ngAfterViewInit() {

  }
  check($event) {
    let checked = $event.target.checked;
    let id = $event.target.id;
    if (checked) {
      this.arrId.push(id);
    } else {
      this.arrId = this.arrId.filter(item => item != id)
    }
  }
  dong() {
    let data = {
      signatures: this.arrId
    }
    this.homeService.hiddenNotification(data).subscribe()
    this.bsModalRef.hide();
  }
}
